.app {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 9fr 3fr;
  grid-template-rows: 11fr 1fr;
  
  .chart {
    width: auto;
  }

  .footbar {
    width: auto;
    margin: 10px;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    box-shadow: 3px 3px 6px #00000033;
    border: solid 1px #c4c4c4;
  }
  
  .sidebar {
    grid-row: span 2;
    min-width: 400px;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    box-shadow: 3px 3px 6px #00000033;
    border: solid 1px #c4c4c4;
    opacity: 1;
    margin: 10px;
    padding: 10px;
  }


}