.tab{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  .active {
    border: 1px solid #2756FF;
    background-color: #2756FF;
    color: #ffffff;
    opacity: 1;
    cursor: pointer;
    width: 100%;
    height: 40px;
  }
  .passive {
    border: 1px solid #2756FF;
    background-color: #ffffff;
    color: #2756FF;
    opacity: 1;
    cursor: pointer;
    width: 100%;
    height: 40px;
  }
}
.home {
  .information{
    display: flex;
    flex-direction: column;
    justify-items: flex-end;
    height: 80vh;
    overflow-y: auto;
    .info {
      height: 87%;
      border: solid 1px #2756FF;
      padding: 5px;
      h2 {
        color: #2756FF;
      }
      strong {
        color: #2756FF;
      }
      label {
        color: #666666;
      }
      .key{
        display: inline-block;
        width: 20%;
        padding: 5px;
      } 
      .value{
        width: 80%;
      }     
    }
    .buttons {
      padding: 10px;
      text-align: right;
    }
  }
}
.create{
  .label {
    display: flex;  
    flex-direction: row;
    height: 50px;
    input {
      font-size: 18px;
      font-weight: bold;
    }
  }
  .properties{
    display: flex;  
    flex-direction: row;
    margin-bottom: 5px;
  }
  .primary{
    margin: 0px 5px 0px 5px;
    height: 50px;
  }
  .select{
    width: 200px;
  }
}